@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 62.5%;
}

body {
  margin: 0;
  background-color: #b1d2f8;
}

.container {
  max-width: 130rem;
  margin: 0 auto;
  padding: 0 3.2rem;
}

input {
  padding: 1.6rem 1.2rem;
  border: none;
  outline: none;
  width: 70%;
  border-radius: 12px;
  margin-bottom: 1.2rem;
  font-size: 1.8rem;
}
.InputExpense {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 2rem 0;
}
label {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.8rem;
  display: inline-block;
}

.btn-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

button {
  padding: 1.6rem 3.8rem;
  border-radius: 12px;
  border: none;
  background-color: #5e00ca;
  color: #fff;
  font-weight: bold;
  font-size: 1.8rem;
  cursor: pointer;
  margin: 1.2rem;
}

.AddExpense {
  background-color: #b197fc;
  padding: 2rem 3.2rem;
  border-radius: 12px;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expenses {
  background-color: #b197fc;
  padding: 2rem 3.2rem;
}

.ExpenseFilter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.filter-name {
  font-size: 1.8rem;
  color: #fff;
  font-weight: bold;
  text-transform: capitalize;
  background-color: #7950f2;
  padding: 1rem 3rem;
  border-radius: 12px;
}
.filter-select-div > select {
  padding: 1rem 2rem;
  font-size: 1.8rem;
  font-weight: bold;
  outline: none;
  border: none;
  border-radius: 12px;
  cursor: pointer;
}
.filter-select-div option {
  padding: 1rem 1.2rem;
  cursor: pointer;
}

.expense-date {
  color: #fff;
  padding: 0.2rem 1.4rem;
  text-align: center;
  font-size: 1.4rem;
}

.expense-month {
  font-weight: bold;
}
.expense-day {
  font-weight: bold;
  font-size: 2.5rem;
}

.Bg-Card {
  background-color: #5e00ca;
  border-radius: 12px;
}

.expense-item {
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 3.2rem;
  font-size: 1.6rem;
  align-items: center;
  margin-bottom: 2rem;
}
.expense-price {
  background-color: #5e00ca;
  padding: 1rem 3rem;
  border-radius: 1rem;
  border: 2px solid #fff;
  color: #fff;
}
.expense-mid {
  font-size: 2rem;
  font-weight: bold;
}

.Test {
  /* background-color: brown; */
}

.expense-list {
  margin-left: -3rem;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}

.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 15rem;
}

