.ExpenseFilter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.filter-name {
  font-size: 1.8rem;
  color: #fff;
  font-weight: bold;
  text-transform: capitalize;
  background-color: #7950f2;
  padding: 1rem 3rem;
  border-radius: 12px;
}
.filter-select-div > select {
  padding: 1rem 2rem;
  font-size: 1.8rem;
  font-weight: bold;
  outline: none;
  border: none;
  border-radius: 12px;
  cursor: pointer;
}
.filter-select-div option {
  padding: 1rem 1.2rem;
  cursor: pointer;
}
