.expense-item {
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 3.2rem;
  font-size: 1.6rem;
  align-items: center;
  margin-bottom: 2rem;
}
.expense-price {
  background-color: #5e00ca;
  padding: 1rem 3rem;
  border-radius: 1rem;
  border: 2px solid #fff;
  color: #fff;
}
.expense-mid {
  font-size: 2rem;
  font-weight: bold;
}
