input {
  padding: 1.6rem 1.2rem;
  border: none;
  outline: none;
  width: 70%;
  border-radius: 12px;
  margin-bottom: 1.2rem;
  font-size: 1.8rem;
}
.InputExpense {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 2rem 0;
}
label {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.8rem;
  display: inline-block;
}

.btn-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

button {
  padding: 1.6rem 3.8rem;
  border-radius: 12px;
  border: none;
  background-color: #5e00ca;
  color: #fff;
  font-weight: bold;
  font-size: 1.8rem;
  cursor: pointer;
  margin: 1.2rem;
}
